.pic {
  cursor: pointer;
  transition: transform 0.6s;
}

.backdrop {
  color: #fff;
  backdrop-filter: blur(8px);
  backdrop-filter: brightness(50%);
}
.image-appear {
  opacity: 0;
  transform: scale(0.8) translateY(10px) translateX(10px);
  z-index: 1;
}
.image-appear.image-appear-active {
  opacity: 1;
  transform: scale(1) translateY(0) translateX(0px);
  transition: opacity 300ms linear 200ms, transform 400ms;
}
.image-enter {
  opacity: 0;
  transform: scale(0.8) translateY(10px) translateX(10px);
  z-index: 1;
}
.image-enter.image-enter-active {
  opacity: 1;
  transform: scale(1) translateY(0) translateX(0px);
  transition: opacity 300ms, transform 400ms;
}
