.App {
  text-align: center;
  overflow-x: hidden;
  background-color: rgb(253, 245, 232);
}
/* width */
::-webkit-scrollbar {
  width: 10px;
}

.scrollbutton-appear {
  opacity: 0;
  transform: translateY(100px) translateX(0px);
  z-index: 1;
}
.scrollbutton-appear.scrollbutton-appear-active {
  opacity: 1;
  transform: translateY(0) translateX(0px);
  transition: opacity 1000ms linear 600ms, transform 1000ms;
}
.scrollbutton-enter {
  opacity: 0;
  transform: translateY(100px) translateX(0px);
  z-index: 1;
}
.scrollbutton-enter.scrollbutton-enter-active {
  opacity: 1;
  transform: translateY(0) translateX(0px);
  transition: opacity 1000ms, transform 1000ms;
}

.scrollbutton-exit {
  opacity: 0;
  transform: translateY(0px) translateX(0px);
  z-index: 1;
}
.scrollbutton-exit.scrollbutton-exit-active {
  opacity: 1;
  transform: translateY(100px) translateX(0px);
  transition: opacity 1000ms linear 1000ms, transform 1000ms;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;

}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 30px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
